
.header {
  position: relative;
  overflow: hidden;
  background: rgba(7,17,27,0.5);
}
.header .content-wrapper {
  position: relative;
  font-size: 0;
  margin: 24px 12px 18px 24px;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .avatar img {
  width: 64px;
  height: 64px;
  border-radius: 2px;
}
.header .content-wrapper .content {
  margin-left: 16px;
