  font-weight: bolder;
  line-height: 18px;
}
.header .content-wrapper .content .description {
  font-size: 12px;
  line-height: 12px;
  font-weight: 200;
  color: #fff;
}
.header .content-wrapper .content .supports {
  margin: 10px 0 2px 0;
}
.header .content-wrapper .content .supports .icon {
