}
@media only screen and (-webkit-device-pixel-ratio: 3) {
.header .content-wrapper .content .supports .icon.special {
    background-image: url("special_1@3x.png");
}
}
.header .content-wrapper .content .supports .text {
  font-size: 10px;
  line-height: 12px;
