}
.header .bulletin-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 26px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #fff;
  background: rgba(7,17,27,0.2);
}
.header .bulletin-wrapper .bulletin-text {
  font-size: 10px;
  font-weight: 200;
}
.header .bulletin-wrapper .bulletin-icon {
  margin-right: 4px;
  margin-top: 9px;
  float: left;
  width: 22px;
