.header .bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .bg img {
  width: 100%;
  height: 100%;
}
.header .mask {
  position: fixed;
  z-index: 99;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.8);
  overflow: auto;
}
.header .mask.fade-enter-active,
.header .mask.fade-leave-active {
  transition: opacity 0.5s;
}
.header .mask.fade-enter,
.header .mask.fade-leave-to {
  opacity: 0;
}
.header .mask .mask-wrapper {
  *zoom: 1;
  min-height: 100%;
}
.header .mask .mask-wrapper::after {
  content: "";
  display: block;
  clear: both;
}
.header .mask .mask-wrapper .mask-content {
  overflow: hidden;
