}
.header .mask .mask-wrapper .mask-content .info {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 24px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.header .mask .mask-wrapper .mask-content .info .text {
  margin: 0 12px 0 12px;
  color: #fff;
}
.header .mask .mask-wrapper .mask-content .info .line {
