}
.header .mask .mask-wrapper .mask-content .list li:last-child {
  margin-bottom: 0px;
}
.header .mask .mask-wrapper .mask-content .list li .icon {
  vertical-align: middle;
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}
.header .mask .mask-wrapper .mask-content .list li .icon.decrease {
  background-image: url("decrease_2@2x.png");
}
@media only screen and (-webkit-device-pixel-ratio: 3) {
