}
.header .mask .mask-wrapper .mask-content .content {
  width: 80%;
  margin: 0 auto;
}
.header .mask .mask-wrapper .mask-content .content p {
  padding: 0 12px;
  font-size: 12px;
  line-height: 24px;
  color: #fff;
  font-weight: 200;
}
.header .mask .mask-footer {
  text-align: center;
  margin-top: -64px;
}
.header .mask .mask-footer span {
  font-size: 32px;
