
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: rgba(7,17,27,0.1);
}
.tab .tab-item {
  width: 0;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item > a {
  font-size: 14px;
  color: #4d555d;
  display: block;
}
.tab .tab-item > a.active {
  color: #f01414;
}


